<template>
  <div>
    <el-date-picker v-model="month" type="month" placeholder="选择月" style="display: block;"></el-date-picker>

    <p class="text-center caption-color">采购周期分析</p>
    <el-table :data="tableData1" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn1" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>

    <el-table :data="tableData2" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle" class="margin-top20">
      <el-table-column v-for="(item ,index) in tableCloumn2" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>
  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'
export default {
  mixins: [cellStyle],
  data () {
    return {
      month: new Date(),
      tableCloumn1: [
        { prop: 'col1', label: '产品编码' },
        { prop: 'col2', label: '部件归类' },
        { prop: 'col3', label: '采购周期' },
      ],
      tableData1: [
        { col1: '111', col2: '', col3: ''},
      ],
      tableCloumn2: [
        { prop: 'col1', label: '供应商' },
        { prop: 'col2', label: '地址' },
        { prop: 'col3', label: '采购周期' },
      ],
      tableData2: [
        { col1: '111', col2: '', col3: ''},
      ],
    }
  }
}
</script>



